// Responsive

@media only screen and (max-width: 767px) {
  .main-nav {
    padding-bottom: 10px !important;
  }
  .intro {
    img {
      display: block;
      margin-left: auto;
      margin-right: auto;
    }

    h1 {
      &:before {
        left: 15% !important;
        top: 100% !important;
        font-size: 130px !important;
      }
    }
  }

  body .to-up {
    padding: 10px 15px !important;
    font-size: 15px;
    position: fixed;
  }

  footer {
    text-align: center;
  }
}
